.consultation {
   min-height: 500px;
   padding: 100px 0 100px 0;
   background: url("../img/dest/bg/bg2.jpg") center (center/cover) no-repeat;
   &__descr {
      margin-top: 8px;
      font-weight: 500;
      font-size: 20px;
      line-height: 30px;
      color: #FFFFFF;
   }
}


@media (max-width: 991px) {
   .consultation {
      &__descr {
          margin-top: 15px;
         text-align: center;
      }
   }
}

